.navbar {
  border: 1px solid red;
}
.sName {
  display: none;
  font-family: 'Comfortaa', cursive;
}

.fName {
  font-family: 'Comfortaa', cursive;
}

@media only screen and (max-width: 600px) {
  .fName {
    display: none;
  }

  .sName {
    display: block;
  }
}

/* Flickering animation */
.neonText {
  animation: flicker 1.5s infinite alternate;
  color: #fff;
}

@keyframes flicker {
    
  0%, 18%, 22%, 25%, 53%, 57%, 100% {

      text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px #0fa,
      0 0 80px #0fa,
      0 0 90px #0fa,
      0 0 100px #0fa;
  
  }
  
  20%, 24%, 55% {        
      text-shadow: none;
  }    
}